import type { SerializedStyles } from '@emotion/react';
import { css } from '@emotion/react';
import { useState, type PropsWithChildren, useRef, useEffect } from 'react';

import { CheckIcon, CloseIcon, LoadingIcon, Typography, useDesignSystemTheme } from '../../design-system';
import type { Theme } from '../../theme';
import { DEFAULT_SPACING_UNIT } from '../../theme/spacing';

export interface Step {
  /**
   * Title of the step
   */
  title: string;

  /**
   * Optional description of the step
   */
  description?: string;

  /**
   * Status of the step. This will change the icon and text color of the step.
   *
   * @default 'upcoming'
   */
  status?: 'completed' | 'loading' | 'upcoming' | 'error';

  icon?: React.ComponentType<{ statusColor: string; status: Step['status'] }>;
}

export interface StepperProps {
  /**
   * List of ordered steps in the stepper
   */
  steps: Step[];

  /**
   * Function to localize the step number; workaround for no react-intl support within dubois
   *
   * ex) localizeStepNumber={intl.formatNumber}
   */
  localizeStepNumber: (stepIndex: number) => string;

  /**
   * Direction of the stepper
   *
   * @default horizontal
   */
  direction?: 'horizontal' | 'vertical';

  /**
   * Current active step from the `steps` property (zero-indexed)
   *
   * @default 0
   */
  currentStepIndex?: number;

  /**
   * If true, and the stepper has a horizontal direction the stepper will be updated to be vertical if width is less than 532px.
   * Set this value to false to opt out of the responsive behavior.
   *
   * @default true
   */
  responsive?: boolean;
}

export function Stepper({
  direction: requestedDirection,
  currentStepIndex: currentStep,
  steps,
  localizeStepNumber,
  responsive = true,
}: StepperProps) {
  const { theme } = useDesignSystemTheme();
  const ref = useRef<HTMLOListElement>(null);
  const { direction } = useResponsiveDirection({ ref, requestedDirection, responsive, enabled: steps.length > 0 });

  if (steps.length === 0) {
    return null;
  }

  const isHorizontal = direction === 'horizontal';
  const currentStepIndex = currentStep ? Math.min(steps.length - 1, Math.max(0, currentStep)) : 0;

  return (
    <ol css={css(getStepsStyle(theme, isHorizontal))} ref={ref}>
      {steps.map((step, index) => {
        const isCurrentStep = index === currentStepIndex;
        const isLastStep = index === steps.length - 1;
        const displayEndingDivider = !isLastStep;
        const contentTitleLevel = isCurrentStep ? 3 : 4;
        const StepIcon = step.icon;
        const {
          icon,
          iconBackgroundColor,
          iconTextColor,
          titleTextColor,
          descriptionTextColor,
          hasStepItemIconBorder,
        } = getStepContentStyleFields(theme, isCurrentStep, step.status, StepIcon);

        return (
          <li aria-current={isCurrentStep} key={index} css={css(getStepItemStyle(theme, isHorizontal, isLastStep))}>
            <StepContentGrid isHorizontal={isHorizontal} isCurrentStep={isCurrentStep}>
              <div
                css={css(getStepItemIconParentStyle(theme, isCurrentStep, iconBackgroundColor, hasStepItemIconBorder))}
              >
                {StepIcon ? (
                  <StepIcon statusColor={iconTextColor} status={step.status} />
                ) : icon ? (
                  <span css={{ color: iconTextColor }}>{icon}</span>
                ) : (
                  <Typography.Title
                    level={contentTitleLevel}
                    css={{ color: `${iconTextColor} !important` }}
                    withoutMargins
                  >
                    {localizeStepNumber(index + 1)}
                  </Typography.Title>
                )}
              </div>
              <Typography.Title
                level={contentTitleLevel}
                withoutMargins
                css={{ flexShrink: 0, color: `${titleTextColor} !important` }}
              >
                {step.title}
              </Typography.Title>
              {displayEndingDivider && (
                <div css={css(getStepEndingDividerStyles(theme, isHorizontal, isCurrentStep))} />
              )}
              {step.description && (
                <Typography.Text
                  css={css(getStepDescriptionStyles(theme, isHorizontal, isLastStep, descriptionTextColor))}
                  withoutMargins
                  size="sm"
                >
                  {step.description}
                </Typography.Text>
              )}
            </StepContentGrid>
          </li>
        );
      })}
    </ol>
  );
}

function getStepsStyle(theme: Theme, isHorizontal: boolean): SerializedStyles {
  return css({
    listStyle: 'none',
    display: 'flex',
    flexDirection: isHorizontal ? 'row' : 'column',
    flexWrap: 'wrap',
    alignItems: 'flex-start',
    gap: isHorizontal ? theme.spacing.md : theme.spacing.sm,
    width: '100%',
    margin: '0',
    padding: '0',
  });
}

function getStepItemStyle(theme: Theme, isHorizontal: boolean, isLastStep: boolean): SerializedStyles {
  return css({
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'flex-start',
    flexGrow: isLastStep ? 0 : 1,
    marginRight: isLastStep ? theme.spacing.lg + theme.spacing.md : 0,
    width: isHorizontal ? undefined : '100%',
  });
}

function getStepContentStyleFields(theme: Theme, isCurrentStep: boolean, status: Step['status'], icon: Step['icon']) {
  const fields = getStepContentStyleFieldsFromStatus(theme, isCurrentStep, status);
  if (icon) {
    return {
      ...fields,
      icon: undefined,
      iconBackgroundColor: undefined,
      iconTextColor: getCustomIconColor(theme, isCurrentStep, status),
      hasStepItemIconBorder: false,
    };
  }

  return fields;
}

function getCustomIconColor(theme: Theme, isCurrentStep: boolean, status: Step['status']): string {
  switch (status) {
    case 'completed':
      return theme.colors.actionDefaultBackgroundPress;
    case 'loading':
      return theme.colors.textPlaceholder;
    case 'error':
      return theme.colors.textValidationDanger;
    default:
    case 'upcoming':
      return isCurrentStep ? theme.colors.actionLinkDefault : theme.colors.textPlaceholder;
  }
}

function getStepContentStyleFieldsFromStatus(
  theme: Theme,
  isCurrentStep: boolean,
  status: Step['status'],
): {
  icon: React.ReactNode | undefined;
  iconBackgroundColor: string | undefined;
  iconTextColor: string;
  titleTextColor: string;
  descriptionTextColor: string;
  hasStepItemIconBorder: boolean;
} {
  switch (status) {
    case 'completed':
      return {
        icon: <CheckIcon />,
        iconBackgroundColor: theme.colors.actionDefaultBackgroundPress,
        iconTextColor: theme.colors.textPlaceholder,
        titleTextColor: theme.colors.textPrimary,
        descriptionTextColor: theme.colors.textSecondary,
        hasStepItemIconBorder: true,
      };
    case 'loading':
      return {
        icon: (
          <LoadingIcon
            spin
            css={{ fontSize: isCurrentStep ? theme.typography.fontSizeXl : theme.typography.fontSizeLg }}
          />
        ),
        iconBackgroundColor: undefined,
        iconTextColor: theme.colors.textPlaceholder,
        titleTextColor: isCurrentStep ? theme.colors.textPrimary : theme.colors.textSecondary,
        descriptionTextColor: theme.colors.textSecondary,
        hasStepItemIconBorder: false,
      };
    case 'error':
      return {
        icon: <CloseIcon css={{ marginBottom: theme.spacing.sm /* center icon in icon circle */ }} />,
        iconBackgroundColor: theme.colors.textValidationDanger,
        iconTextColor: 'white',
        titleTextColor: theme.colors.textValidationDanger,
        descriptionTextColor: theme.colors.textValidationDanger,
        hasStepItemIconBorder: false,
      };
    default:
    case 'upcoming':
      if (isCurrentStep) {
        return {
          icon: undefined,
          iconBackgroundColor: theme.colors.actionLinkDefault,
          iconTextColor: 'white',
          titleTextColor: theme.colors.textPrimary,
          descriptionTextColor: theme.colors.textPrimary,
          hasStepItemIconBorder: false,
        };
      }
      return {
        icon: undefined,
        iconBackgroundColor: undefined,
        iconTextColor: theme.colors.textPlaceholder,
        titleTextColor: theme.colors.textSecondary,
        descriptionTextColor: theme.colors.textSecondary,
        hasStepItemIconBorder: true,
      };
  }
}

const MaxHorizontalStepDescriptionWidth = 140;
const CurrentStepIconSize = DEFAULT_SPACING_UNIT * 4;
const NonCurrentStepIconSize = DEFAULT_SPACING_UNIT * 3;

function getStepItemIconParentStyle(
  theme: Theme,
  isCurrentStep: boolean,
  iconBackgroundColor: string | undefined,
  hasStepItemIconBorder: boolean,
): SerializedStyles {
  return css({
    width: isCurrentStep ? CurrentStepIconSize : NonCurrentStepIconSize,
    height: isCurrentStep ? CurrentStepIconSize : NonCurrentStepIconSize,
    backgroundColor: iconBackgroundColor,
    borderRadius: '50%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    fontSize: '20px',
    flexShrink: 0,
    border: hasStepItemIconBorder ? `1px solid ${theme.colors.textPlaceholder}` : undefined,
  });
}

function getStepEndingDividerStyles(theme: Theme, isHorizontal: boolean, isCurrentStep: boolean): SerializedStyles {
  const backgroundColor = isCurrentStep ? theme.colors.actionLinkDefault : theme.colors.border;

  if (isHorizontal) {
    return css({
      backgroundColor,
      height: '1px',
      width: '100%',
      minWidth: theme.spacing.md,
    });
  }
  return css({
    backgroundColor,
    height: '100%',
    minHeight: theme.spacing.md,
    width: '1px',
    alignSelf: 'flex-start',
    marginLeft: theme.spacing.md,
  });
}

function getStepDescriptionStyles(
  theme: Theme,
  isHorizontal: boolean,
  isLastStep: boolean,
  textColor: string,
): SerializedStyles {
  return css({
    alignSelf: 'flex-start',
    width: '100%',
    gridColumn: isHorizontal || isLastStep ? '2 / span 2' : undefined,
    maxWidth: isHorizontal ? MaxHorizontalStepDescriptionWidth : undefined,
    paddingBottom: isHorizontal ? undefined : theme.spacing.sm,
    color: `${textColor} !important`,
  });
}

function StepContentGrid({
  children,
  isHorizontal,
  isCurrentStep,
}: PropsWithChildren<{ isHorizontal: boolean; isCurrentStep: boolean }>) {
  const { theme } = useDesignSystemTheme();
  if (isHorizontal) {
    return (
      <div
        css={{
          display: 'grid',
          gridTemplateColumns: `${
            isCurrentStep ? CurrentStepIconSize : NonCurrentStepIconSize
          }px fit-content(100%) 1fr`,
          gridTemplateRows: `${CurrentStepIconSize}px auto`,
          alignItems: 'center',
          justifyItems: 'flex-start',
          gridColumnGap: theme.spacing.sm,
          gridRowGap: theme.spacing.sm,
          width: '100%',
        }}
      >
        {children}
      </div>
    );
  }
  return (
    <div
      css={{
        display: 'grid',
        gridTemplateColumns: `${CurrentStepIconSize}px auto`,
        alignItems: 'center',
        justifyItems: 'flex-start',
        gridColumnGap: theme.spacing.md,
        gridRowGap: theme.spacing.sm,
        width: '100%',
        '& > :first-child': {
          // horizontally center the first column (circle/icon)
          justifySelf: 'center',
        },
      }}
    >
      {children}
    </div>
  );
}

// Ant design uses the same value for their stepper and to works well for us as well.
const MinimumHorizonalDirectionWidth = 532;

// exported for unit test
export function useResponsiveDirection({
  requestedDirection = 'horizontal',
  responsive,
  enabled,
  ref,
}: {
  requestedDirection: StepperProps['direction'];
  enabled: boolean;
  responsive: boolean;
  ref: React.RefObject<HTMLOListElement>;
}) {
  const [direction, setDirection] = useState(requestedDirection);

  useEffect(() => {
    if (requestedDirection === 'vertical' || !enabled || !responsive || !ref.current) {
      return;
    }

    let timeoutId: number;
    const resizeObserver = new ResizeObserver((entries) => {
      timeoutId = requestAnimationFrame(() => {
        if (entries.length === 1) {
          const width = entries[0].target.clientWidth || 0;
          setDirection(width < MinimumHorizonalDirectionWidth ? 'vertical' : 'horizontal');
        }
      });
    });
    if (ref.current) {
      resizeObserver.observe(ref.current);
    }
    return () => {
      resizeObserver.disconnect();
      cancelAnimationFrame(timeoutId);
    };
  }, [requestedDirection, enabled, ref, responsive]);

  return { direction };
}
